/* Style for the plan cards */
.plan-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.plan-card {
  flex: 1;
  padding: 20px;
  border: 1px solid #ddd;
  background-color: #fff;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.plan-card h3 {
  margin: 0;
}

.plan-card p {
  margin: 5px 0;
}

.plan-card:hover {
  background-color: #f0f0f0;
}

.plan-card.selected {
  background-color: #007bff;
  color: #fff;
}

/* Style for the selected plan */
.selected-plan {
  margin-top: 20px;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.selected-plan p {
  margin: 5px 0;
}

  /* Style for the toggle buttons */
  .toggle-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 3rem 0 1rem 0;
    padding: 1rem 0 1rem 0;
    background-color: var(--magnolia);
  }
  .toggle-container input[type=checkbox] {
    height: 0;
    width: 0;
    visibility: hidden;
  }
  
  .toggle-container label {
    cursor: pointer;
    text-indent: -9999px;
    width: 50px; 
    height: 25px; 
    background:var(--marine-blue);
    display: block;
    border-radius: 25px; 
    position: relative;
  }
  
  .toggle-container label:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    width: 15px; 
    height: 15px; 
    background: #fff;
    border-radius: 50%; 
    transition: 0.3s;
  }
  
  /* .toggle-container input:checked + label {
    background: #bada55;
  } */
  
  .toggle-container input:checked + label:after {
    left: calc(100% - 5px);
    transform: translateX(-100%);
  }
  
  .toggle-container label:active:after {
    width: 20px; 
    height: 20px; 
  }

  .toggle-container p{
    margin: 0 1rem 0 1rem;
    color: var(--marine-blue)
  }

  /* icon style */

  .image-container {
    width: 200px; 
    height: 200px; 
    border: 2px solid #333; 
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--marine-blue); 
  }
  
  img {
    width: 80%; 
    height: auto; 
    max-height: 80%;
    filter: grayscale(50%);
  }
  
  
  
  
  