
/* Style for the entire component */
.add-on-selector {
    font-family: Arial, sans-serif;
    margin: 0 auto;
    padding: 20px;
    background-color: var(--white);
    border: 1px solid var(--cool-gray);
    border-radius: 5px;
    box-shadow: 0px 0px 5px var(--marine-blue);
  }
  
  /* Style for the add-on cards */
  .add-on-container h2, .add-on-container h3{
    color: var(--marine-blue);
  }

  .add-on-container h5, .add-on-container p{
    color: var(--cool-gray)
  }
  .add-on-card {
    padding: 10px;
    border: 1px solid #ddd;
    margin: 10px 0;
    background-color: #fff;
    border-radius: 5px;
  }
  
  .add-on-card h3 {
    margin: 0;
  }
  
  .add-on-card p {
    margin: 5px 0;
  }

  .add-on-card h5{
    float: right;
  }

  .add-on-card label {
    display: block;
    margin-top: 10px;
    cursor: pointer;
  }
  
  /* Style for the selected add-ons list */
  .selected-add-ons {
    margin-top: 20px;
  }
  
  .selected-add-ons h3 {
    margin-bottom: 10px;
  }
  
  .selected-add-ons ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .selected-add-ons li {
    margin: 5px 0;
  }
  